
          @import "@/assets/themes/AppAddict/variables";
          @import "@/assets/core";
        

@import "@/assets/variables";

.main {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;

  @include notebook {
    flex-direction: column;
  }

  &__filters {
    width: 100%;

    @include notebook {
      order: 1;
    }
  }

  &__filters-title {
    display: flex;
    gap: 6px;
    align-items: center;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: 600;

    @include notebook {
      font-size: 18px;
      margin-bottom: 12px;
    }

    img {
      height: 15px;
      margin-top: 2px;

      @include notebook {
        height: 17px;
      }
    }
  }

  &__content {
    flex-grow: 1;
    max-width: 100%;

    @include notebook {
      order: 2;
    }
  }

  &__sidebar {
    width: 370px;

    @include notebook {
      width: 100%;
      order: 0;
    }
  }

  &__apps {
    &:not(:last-child) {
      margin-bottom: 40px;

      @include notebook {
        margin-bottom: 30px;
      }
    }
  }
}
